body {
  font-family: sans-serif;
}

h1 {
  text-align: center;
  margin: 20px auto;
  font-size: 30px;
  font-weight: bold;
}
#app {
  margin: 30px auto;
  max-width: 500px;
}

.inline-form {
  display: flex;
  flex-flow: columns;
  gap: 5px;
}

#output {
  background: #d4f7ff;
  border: 1px solid #ced4da;
  display: none;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  box-shadow: 0 2px 2px #eee;
}
